.products-search{
    display: flex;
    background-color: var(--surface-200);
    border-radius: 10px;
    width: 275px;
    height: 35px;

    .pi{
        width: 25px;
        font-size: 1rem;
        color: $primary-color;
        margin-top: 10px;
        margin-left: 10px;
    }

    &-input{
        height: 35px;
        background-color: transparent;
        color: var(surface-600);
        border: 0;

        //取消css input box 被點下預設會出現之邊框
        &:focus, &:active{
            border: 0;
            outline: none;
        }
    }
}