.login-register{
    //背景漸層
    background-image: linear-gradient(235deg, #565867, #191a25);
    height: 100vh;

    &-panel{ //中間的login 視窗
        width: 650px;
        height: 525px;
        background-color: var(--surface-a); //使用PrimeNG顏色

        //使其無論如何都置中
        position: fixed;
        left: calc(50% - 325px); //扣去width一半
        top: calc(50% - 260px);//扣去高一半

        //把submit按鈕放大跟其容器一樣大
        .full-btn{
            width: 100%;
        }
    }
}